.sidebar {
  background-color: #dfe0e0;
  height: 100%;
  width: 22vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.text {
  padding: 3vw;
  text-align: center;
  color: #263547;
}

.imageContainer {
  z-index: 3;
  padding: 3vw;
  display: flex;
  justify-content: center;
}


@media screen and (max-width: 699px){
  .sidebar{
    width: 100vw;
  }
}